    <md-button class="md-accent md-raised" @click="useTemplate('template03')" v-if="manager.homepageService.homepage.template !== 'template03'">このテンプレートを使用</md-button>
    <md-button class="md-accent md-raised" disabled v-else>使用中</md-button>
    <div class="detail">
      <p class="md-body-1">サービスが充実な会社に向いています。トピックス機能はあえて使っていません。</p>
    </div>
  </div>
  <div class="md-layout-item md-size-50 md-xsmall-size-100">
    <template04 :manager="manager" :template="manager.const.template.template04"/>
    <md-button class="md-accent md-raised" @click="useTemplate('template04')" v-if="manager.homepageService.homepage.template !== 'template04'">このテンプレートを使用</md-button>
    <md-button class="md-accent md-raised" disabled v-else>使用中</md-button>
    <div class="detail">
      <p class="md-body-1">ホームページのビジョンを重視するデザイン、物件情報などコンテンツは別ページにて展開できます。</p>
    </div>
  </div>
  <div class="md-layout-item md-size-50 md-xsmall-size-100">
    <template05 :manager="manager" :template="manager.const.template.template05"/>
    <md-button class="md-accent md-raised" @click="useTemplate('template05')" v-if="manager.homepageService.homepage.template !== 'template05'">このテンプレートを使用</md-button>
    <md-button class="md-accent md-raised" disabled v-else>使用中</md-button>
    <div class="detail">
      <p class="md-body-1">クラシックな画面配置で使用者へのインパクトを抑えめにしています。</p>
    </div>
  </div>
  <div class="md-layout-item md-size-50 md-xsmall-size-100">
    <template06 :manager="manager" :template="manager.const.template.template06"/>
    <md-button class="md-accent md-raised" @click="useTemplate('template06')" v-if="manager.homepageService.homepage.template !== 'template06'">このテンプレートを使用</md-button>
    <md-button class="md-accent md-raised" disabled v-else>使用中</md-button>
    <div class="detail">
      <p class="md-body-1">フラットなデザインでエレガントなイメージを与えます。必要な機能も全て盛り込んでいます。</p>
    </div>
  </div>
  <div class="md-layout-item md-size-50 md-xsmall-size-100">
    <template07 :manager="manager" :template="manager.const.template.template07"/>
    <md-button class="md-accent md-raised" @click="useTemplate('template07')" v-if="manager.homepageService.homepage.template !== 'template07'">このテンプレートを使用</md-button>
    <md-button class="md-accent md-raised" disabled v-else>使用中</md-button>
    <div class="detail">
      <p class="md-body-1">サービスを単独なページに配置して自社のサービスの充実さをアピールします。トピックスと資料ダウンロード機能でお客様とのコニュニケーションも捗ります。</p>
    </div>
  </div>
  <div class="md-layout-item md-size-50 md-xsmall-size-100">
    <template08 :manager="manager" :template="manager.const.template.template08"/>
    <md-button class="md-accent md-raised" @click="useTemplate('template08')" v-if="manager.homepageService.homepage.template !== 'template08'">このテンプレートを使用</md-button>
    <md-button class="md-accent md-raised" disabled v-else>使用中</md-button>
    <div class="detail">
      <p class="md-body-1">サービスと物件情報を強調します。トピックス機能はあえて取り除いて、実用性を最大限にするデザインです。</p>
    </div>
  </div>
  <div class="md-layout-item md-size-50 md-xsmall-size-100">
    <template09 :manager="manager" :template="manager.const.template.template09"/>
    <md-button class="md-accent md-raised" @click="useTemplate('template09')" v-if="manager.homepageService.homepage.template !== 'template09'">このテンプレートを使用</md-button>
    <md-button class="md-accent md-raised" disabled v-else>使用中</md-button>
    <div class="detail">
      <p class="md-body-1">クラシックな画面配置で、ダウンロード機能も搭載しています。</p>
    </div>
  </div>
  <div class="md-layout-item md-size-50 md-xsmall-size-100">
    <template10 :manager="manager" :template="manager.const.template.template10"/>
    <md-button class="md-accent md-raised" @click="useTemplate('template10')" v-if="manager.homepageService.homepage.template !== 'template10'">このテンプレートを使用</md-button>
    <md-button class="md-accent md-raised" disabled v-else>使用中</md-button>
    <div class="detail">
      <p class="md-body-1">カバー画像を取り除いて、さらなるシンプルさを追求。</p>
    </div>
  </div>
  <div class="md-layout-item md-size-50 md-xsmall-size-100">
    <template11 :manager="manager" :template="manager.const.template.template11"/>
    <md-button class="md-accent md-raised" @click="useTemplate('template11')" v-if="manager.homepageService.homepage.template !== 'template11'">このテンプレートを使用</md-button>
    <md-button class="md-accent md-raised" disabled v-else>使用中</md-button>
    <div class="detail">
      <p class="md-body-1">タグを活用して、物件を分類してホームページに載せれます。タグの種類はご自由にお選びいただけます。</p>
    </div>
  </div>
  <div class="md-layout-item md-size-50 md-xsmall-size-100">
    <template12 :manager="manager" :template="manager.const.template.template12"/>
    <md-button class="md-accent md-raised" @click="useTemplate('template12')" v-if="manager.homepageService.homepage.template !== 'template12'">このテンプレートを使用</md-button>
    <md-button class="md-accent md-raised" disabled v-else>使用中</md-button>
    <div class="detail">
      <p class="md-body-1">タグが使えるテンプレートです。サービスもホームページに載せてアピールできるようにしています。</p>
    </div>
  </div>
  <div class="md-layout-item md-size-50 md-xsmall-size-100">
    <template13 :manager="manager" :template="manager.const.template.template13"/>
    <md-button class="md-accent md-raised" @click="useTemplate('template13')" v-if="manager.homepageService.homepage.template !== 'template13'">このテンプレートを使用</md-button>
    <md-button class="md-accent md-raised" disabled v-else>使用中</md-button>
    <div class="detail">
      <p class="md-body-1">フラットなデザインで、ホームページにタグを駆使してたくさんの物件情報を載せて、お客様にとって一番有益な情報を詰め込んだテンプレートです。</p>
    </div>
  </div>
  <div class="md-layout-item md-size-50 md-xsmall-size-100">
    <template14 :manager="manager" :template="manager.const.template.template14"/>
    <md-button class="md-accent md-raised" @click="useTemplate('template14')" v-if="manager.homepageService.homepage.template !== 'template14'">このテンプレートを使用</md-button>
    <md-button class="md-accent md-raised" disabled v-else>使用中</md-button>
    <div class="detail">
      <p class="md-body-1">タグ機能をフルで使える上、おしゃれなデザインを採用しています。</p>
    </div>
  </div>
  <div class="md-layout-item md-size-50 md-xsmall-size-100">
    <template15 :manager="manager" :template="manager.const.template.template15"/>
    <md-button class="md-accent md-raised" @click="useTemplate('template15')" v-if="manager.homepageService.homepage.template !== 'template15'">このテンプレートを使用</md-button>
    <md-button class="md-accent md-raised" disabled v-else>使用中</md-button>
    <div class="detail">
      <p class="md-body-1">クラシックな配置に加え、タグで物件を分類できます。</p>
    </div>
  </div>
  <div class="md-layout-item md-size-50 md-xsmall-size-100">
    <template16 :manager="manager" :template="manager.const.template.template16"/>
    <md-button class="md-accent md-raised" @click="useTemplate('template16')" v-if="manager.homepageService.homepage.template !== 'template16'">このテンプレートを使用</md-button>
    <md-button class="md-accent md-raised" disabled v-else>使用中</md-button>
    <div class="detail">
      <p class="md-body-1">物件情報をホームページに集中し、それ以外の情報をメニューに寄せて、集客を目的にしています。</p>
    </div>
  </div>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import template01 from './template/template01'
import template02 from './template/template02'
import template03 from './template/template03'
import template04 from './template/template04'
import template05 from './template/template05'
import template06 from './template/template06'
import template07 from './template/template07'
import template08 from './template/template08'
import template09 from './template/template09'
import template10 from './template/template10'
import template11 from './template/template11'
import template12 from './template/template12'
import template13 from './template/template13'
import template14 from './template/template14'
import template15 from './template/template15'
import template16 from './template/template16'
export default {
  props: ['manager', 'user'],
  components: {
    template01,
    template02,
    template03,
    template04,
    template05,
    template06,
    template07,
    template08,
    template09,
    template10,
    template11,
    template12,
    template13,
    template14,
    template15,
    template16
  },
  methods: {
    useTemplate (template) {
      const that = this
      let isDomainValid = true
      if (!manager.homepageService.homepage.domain) isDomainValid = false
      else if (manager.homepageService.homepage.domain.indexOf('http') === 0) isDomainValid = false
      else if (manager.homepageService.homepage.domain.length > 50) isDomainValid = false
      else if (manager.homepageService.homepage.isDuplicate) isDomainValid = false
      else if (manager.homepageService.homepage.domain.indexOf('/') >= 0) isDomainValid = false
      if (!isDomainValid) {
        utils.event.$emit('SHOW_MESSAGE', {code: 'B011'})
        return
      }
      manager.homepageService.homepage.template = template
      manager.homepageService.homepage.pages = manager.const.template[template]
      if (manager.homepageService.homepage._id) {
        utils.event.$emit('SHOW_MESSAGE', {code: 'I010', detail: '切り替えられるのはレイアウトのみで、入力された文字情報などはそのまま保持されます。'}, () => {
          manager.homepageService.updateHomepage(() => {
            manager.homepageService.page = manager.homepageService.homepage.pages[0]
            manager.homepageService.showTemplate = false
          })
        })
      } else {
        manager.homepageService.insertHomepage(() => {
          if (!that.user.homepages) that.user.homepages = []
          that.user.homepages.push(manager.homepageService.homepage.domain)
          utils.restPost('/private/updateUser', that.user.toJSON()).then(
            response => {
              if (response) {
                manager.user.signin(response)
                manager.homepageService.showTemplate = false
              }
            }
          )
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.md-layout-item {
  .md-button {
    width: calc(100% - 16px);
  }
  margin-bottom: 40px;
}
.detail {
  width: 100%;
  padding: 0 16px;
  margin-bottom: 16px;
  p {
    margin: 0;
  }
}
</style>



